html {
  /*font-size: 100%;*/
  min-height: 100%;

}

body {
  background-color: rgba(143, 143, 143, 0.144);
  margin: 0;
  overflow-x: hidden;
  min-height: 100%;
}
#root {
  min-height: 100%;
}

.page {
  width: 61rem;
  margin: auto;
  min-height: 100%;
}

.content {
  padding: 3.8rem 0 0 0;
  margin: 0 1rem 0 8rem;
  min-height: calc(100% - 3.8rem);
}

.send-button__img {
  height: 1.5rem;
  opacity: 0.7;
}

.send-button__button {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.app-error {
  position: fixed;
  display: table;
  text-align: center;
  width: 15.625rem;
  height: 6.25rem;
  z-index: 2000;
  right: 12.5rem;
  top: 0rem;
  background-color: white;
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.8rem;
  color: #ff7575;
  border-radius: 0.25rem;
  padding: 0.15rem;
}

.app-error__text {
  display: table-cell;
  vertical-align: middle;
}


.app-error-enter {
  top: -6.25rem;
}

.app-error-enter-active {
  transition: top 1000ms;
  top: 0.625rem;
}

.app-error-enter-done {
  top: 0.625rem;
}

.app-error-exit {
  top: 0.625rem;
}

.app-error-exit-active {
  transition: top 500ms;
  top: -6.25rem;
}

.app-error-exit-done {
  top: -6.25rem;
}

.message__text iframe {
  margin-top: 0.5rem;
}

.emoji-button {
  background-image: url(./assets/img/smile.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  cursor: pointer;
  opacity: 0.7;
}

.emoji-button:hover {
  background-image: url(./assets/img/smileYellow.gif);
  opacity: 1;
}

.error-404__wrapper, .work-in-progress__wrapper {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
}

.error-404, .work-in-progress {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  width: 15rem;
  background-color: white;
  border-radius: 0.25rem;
  border: solid 0.0625rem rgba(128, 128, 128, 0.200);
}




::-webkit-scrollbar-button {
  background-image:url('');
  background-repeat:no-repeat;
  width: 0.3125rem;
  height:0;
}

::-webkit-scrollbar-track {
  background-color:#ecedee
}

::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0;
  border-radius: 0;
  background-color:#6dc0c8;
}

::-webkit-scrollbar-thumb:hover{
  background-color:#56999f;
}

::-webkit-resizer{
  background-image:url('');
  background-repeat:no-repeat;
  width: 0.25rem;
  height:0;
}

::-webkit-scrollbar{
  width: 0.25rem;
}