html {
  /*font-size: 100%;*/
  min-height: 100%;

}

body {
  background-color: rgba(143, 143, 143, 0.144);
  margin: 0;
  overflow-x: hidden;
  min-height: 100%;
}
#root {
  min-height: 100%;
}

.page {
  width: 61rem;
  margin: auto;
  min-height: 100%;
}

.content {
  padding: 3.8rem 0 0 0;
  margin: 0 1rem 0 8rem;
  min-height: calc(100% - 3.8rem);
}

.send-button__img {
  height: 1.5rem;
  opacity: 0.7;
}

.send-button__button {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.app-error {
  position: fixed;
  display: table;
  text-align: center;
  width: 15.625rem;
  height: 6.25rem;
  z-index: 2000;
  right: 12.5rem;
  top: 0rem;
  background-color: white;
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.8rem;
  color: #ff7575;
  border-radius: 0.25rem;
  padding: 0.15rem;
}

.app-error__text {
  display: table-cell;
  vertical-align: middle;
}


.app-error-enter {
  top: -6.25rem;
}

.app-error-enter-active {
  -webkit-transition: top 1000ms;
  transition: top 1000ms;
  top: 0.625rem;
}

.app-error-enter-done {
  top: 0.625rem;
}

.app-error-exit {
  top: 0.625rem;
}

.app-error-exit-active {
  -webkit-transition: top 500ms;
  transition: top 500ms;
  top: -6.25rem;
}

.app-error-exit-done {
  top: -6.25rem;
}

.message__text iframe {
  margin-top: 0.5rem;
}

.emoji-button {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  cursor: pointer;
  opacity: 0.7;
}

.emoji-button:hover {
  background-image: url(data:image/gif;base64,);
  opacity: 1;
}

.error-404__wrapper, .work-in-progress__wrapper {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
}

.error-404, .work-in-progress {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  width: 15rem;
  background-color: white;
  border-radius: 0.25rem;
  border: solid 0.0625rem rgba(128, 128, 128, 0.200);
}




::-webkit-scrollbar-button {
  background-image:url('');
  background-repeat:no-repeat;
  width: 0.3125rem;
  height:0;
}

::-webkit-scrollbar-track {
  background-color:#ecedee
}

::-webkit-scrollbar-thumb {
  border-radius: 0;
  background-color:#6dc0c8;
}

::-webkit-scrollbar-thumb:hover{
  background-color:#56999f;
}

::-webkit-resizer{
  background-image:url('');
  background-repeat:no-repeat;
  width: 0.25rem;
  height:0;
}

::-webkit-scrollbar{
  width: 0.25rem;
}
.navbar {
  float: left;
  padding-top: 4.8rem;
  position: fixed;
}

.navbar-list {
  margin: 0;
  list-style-type: none;
  padding: 0;
  width: 7rem;
}

.navbar-list__item {
  padding: 0 0 0 0.5rem;
  height: 1.6rem;
  font-family: 'Ubuntu', sans-serif;
}

.navbar-list__item-link {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: rgb(56, 56, 56);
}

.navbar-list__item:hover {
  cursor: pointer;
  background-color: rgb(255, 255, 255);
}

.navbar-list__item-profile-img {
  width: 1rem;
  height: 1rem;
}

.navbar-list__item-users-img {
  width: 1.25rem;
  height: 1.25rem;
}

.navbar-list__item-dialogs-img {
  width: 1.25rem;
  height: 1.25rem;
}

.navbar-list__item-news-img {
  width: 1.25rem;
  height: 1rem;
}

.navbar-list__item-profile-word {
  margin-left: 0.5rem;
}

.navbar-list__item-users-word {
  margin-left: 0.25rem;
}

.navbar-list__item-dialogs-word {
  margin-left: 0.25rem;
}

.navbar-list__item-news-word {
  margin-left: 0.25rem;
}
.dialogs-page {
  min-height: 100%;
}

.dialogs-field {
  width: 35rem;
  background-color: white;
  border-radius: 0.25rem;
  position: absolute;
  margin-top: 3.8rem;
  margin-bottom: 1rem;
  top: 0;
  min-height: calc(100% - (4.8rem + 0.125rem));
  border: solid 0.0625rem rgba(128, 128, 128, 0.200);
}

.contacts-list {
  padding: 0.5rem;
  overflow-y: auto;
  overflow-x: hidden;
  margin-left: 35.5rem;
  position: fixed;
  height: 14rem;
  width: 15.375rem;
  background-color: white;
  border: solid 0.0625rem rgba(128, 128, 128, 0.200);
  border-radius: 0.25rem;
}

.dialogs-field__messages {
  margin: 1rem 0rem 8rem 0rem;
  font-family: 'Cuprum', sans-serif;
}

.dialogs-field__chat-wrapper {
  position: fixed;
  bottom: 1rem;
  min-height: 4rem;
  width: 35rem;
  z-index: 1000;
  background-color: white;
  border-top: solid 0.0625rem rgba(95, 95, 95, 0.164);
  border-bottom: solid 0.0625remx rgba(143, 143, 143, 0.144);
}

.dialogs-field__chat {
  bottom: 3rem;
  min-height: 1rem;
  width: 26rem;
  z-index: 1000;
  background-color: white;
  border: solid 0.0625rem rgba(95, 95, 95, 0.295);
  border-radius: 0.25rem;
  padding: 0.5rem;
  margin: 0.94rem 0 1rem 2em;
  word-wrap: break-word;
}

.dialogs-field__chat  div {
  outline:none;
  min-height: 1rem;
  font-family: 'Cuprum', sans-serif;
}

.dialogs-field__chat > div:empty:before {
  content: attr(placeholder);
  display: block;
  color: #aaa;
  cursor: text;
}

.dialogs-field__send-button-wrapper {
  margin: 1.08rem 1rem 0 0;
  float: right;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2rem;
  width: 2rem;
  background-color: rgb(189, 242, 255);
  border-radius: 0.25rem;
}

.dialogs-field__send-button-wrapper:hover {
  cursor: pointer;
}

.dialogs-field__send-button-wrapper:hover img {
  opacity: 1;
}

.dialogs-field__emoji-button {
  float: right;
  margin-top: 1.3rem;
  margin-right: 0.5rem;
  height: 1.5rem;
  width: 1.5rem;
}

.bottom-hider {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1rem;
  background-color: #eeeeee;
  z-index: 1100;
}

.message-wrapper:hover, .additional-text__wrapper:hover {
  background-color: rgba(105, 167, 255, 0.3);
}

.message-wrapper {
  margin: 1.5rem 0.5rem 0 0.5rem;
  min-height: 3.625rem;
  position: relative;
}

.message-wrapper:hover .message__delete-btn {
  display: block;
}

.additional-text__wrapper:hover .message__delete-btn {
  display: block;
}

.message__delete-btn:hover {
  opacity: 0.8;
}

.message__delete-btn {
  display: none;
  position: absolute;
  right: 1.2rem;
  top: 0.1rem;
  height: 1rem;
  width: 1rem;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  opacity: 0.4;
  cursor: pointer;
  z-index: 1000;
}

.message {
  padding: 0 3.5rem 0.2rem 0.5rem;
}

.message:first-child {
  margin-top: 0;
}

.message__avatar {
  margin: 0.25rem 0 0.25rem 0;
  height: 3.125rem;
  width: 3.125rem;
  float: left;
  border-radius: 0.25rem;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.message__body {
  margin-left: 4.375rem;
  line-height: 120%;
}

.message__from {
  font-size: 1.1rem;
  font-style: italic;
  font-weight: 400;
  display: inline;
  font-family: 'Roboto', sans-serif;
}

.message__time {
  margin-left: 0.5rem;
  color: #a5a5a5;
}

.message__date {
  float: right;
  color: #a5a5a5;
}

.message__text {
  margin: 0.3rem 0 0 0;
}

.additional-text__wrapper {
  margin: 0 0.5rem 0 0.5rem;
  position: relative;
}

.messages__additional-text {
  margin: 0 0 0 0;
  padding: 0.5rem 3.5rem 0.3rem 4.875rem;
}

.message-is-unread {
  background-color: rgba(105, 167, 255, 0.1);
}

.contact {
  position: relative;
  background: -webkit-gradient(linear, right top, left top, from(rgba(176, 224, 230, 0.329)), to(white));
  background: linear-gradient(to left, rgba(176, 224, 230, 0.329), white);
  padding: 0.2rem;
  margin-top: 0.2rem;
  width: 100%;
  height: 2.25rem;
}

.contact:first-child {
  margin-top: 0;
}

.contact:hover {
  cursor: pointer;
  background: -webkit-gradient(linear, right top, left top, from(rgba(176, 224, 230, 0.87)), to(white));
  background: linear-gradient(to left, rgba(176, 224, 230, 0.87), white);
}

.contact__avatar {
  display: block;
  float: left;
}

.contact__avatar-img {
  position: relative;
  height: 2.25rem;
  width: 2.25rem;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  -webkit-transition: -webkit-transform 500ms;
  transition: -webkit-transform 500ms;
  transition: transform 500ms;
  transition: transform 500ms, -webkit-transform 500ms;
}

.contact__avatar-img:hover {
  -webkit-transform: scale(1.2,1.2);
          transform: scale(1.2,1.2);
}

.contact__last-online {
  position: absolute;
  display: none;
  width: 10rem;
  left: 2.5rem;
  top: 1.5rem;
  color: gray;
  font-family: 'Cuprum', sans-serif;
  font-size: 0.6rem;
  z-index: 1000;
}

.contact__avatar-img:hover .contact__last-online{
 display: block;
}

.contact-info {
  margin-left: 2.25rem;
  height: 100%;
}

.contact__name {
  margin-left: 0.5rem;
  font-family: 'Cuprum', sans-serif;
  font-size: 1.1rem;
}

.contact__online-status {
  position: absolute;
  right: 0;
  margin-right: 0.3rem;
  bottom: 0;
  font-family: 'Cuprum', sans-serif;
  font-size: 0.8rem;
  opacity: 0.8;
}

.contact__online-status--online {
  color: green;
}

.contact__online-status--offline {
  color: gray;
}

.contact__new-messages {
  display: inline-block;
  position: relative;
  bottom: 0.3rem;
  padding: 0.05rem 0.3rem 0.05rem 0.3rem;
  margin-left: 0.5rem;
  font-size: 0.7rem;
  font-family: 'Cuprum', sans-serif;
  color: white;
  opacity: 0.7;
  background-color: red;
  border-radius: 0.25rem;
}

.dialog-with {
  background-color: rgba(109, 177, 200, 1);
}

.loading-container {
  position: absolute;
  width: 35rem;
  height: 100%;
}

.loading-svg {
  height: 10rem;
  width: 10rem;
  margin: 40% auto 0 auto;
  background-image: url(/qq/static/media/loading.eb4dab25.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.dialogs-field__messages iframe {
  width: 25.5rem;
  height: 14.5rem;
}
