.header {
  height: 2.8rem;
  width: 100%;
  background-color: rgb(189, 242, 255);
  position: fixed;
  z-index: 1000;
}

.siteLogo {
  display: flex;
  align-items: center;
  font-family: 'Amaranth', sans-serif;
  height: 100%;
  width: 4rem;
  float: left;
  font-size: 1.8rem;
  margin-left: 1rem;
  color: black;
}

.exitButton {
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
  margin-right: 2rem;
  height: 2.8rem;
  font-family: 'Teko', sans-serif;
  font-size: 1.3rem;
  font-weight: 600;
}

.exitButton:hover {
  cursor: pointer;
  color: rgb(82, 82, 82);
}
.users-page {
  position: relative;
  margin-bottom: 1rem;
}


/*начало стилей для paginator*/
.users-page .page-buttons {
  position: relative;
  display: flex;
  justify-content: left;
  height: 1.8rem;
  width: 38rem;
  background-color: white;
  border: solid 0.0625rem rgba(128, 128, 128, 0.200);
  border-radius: 0.25rem;
}

.page-buttons > ul {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  list-style-type: none;
  padding: 0;
  margin: 0 0 0 3.4rem;
  font-family: 'Cuprum', sans-serif;
  height: 100%;
}

.page-buttons li {
  font-size: 0.9rem;
  width: 1.5rem;
  text-align: center;
  border-right: solid 0.0625rem rgba(128, 128, 128, 0.185);
}

.page-buttons li:first-child {
  border-left: solid 0.0625rem rgba(128, 128, 128, 0.185);
}

.page-buttons a {
  text-decoration: none;
  color: rgb(59, 59, 59);
}

.page-buttons__previous-page {
  position: absolute;
  height: 100%;
  left: 0px;
  margin-left: 0.5rem;
  display: flex;
  align-items: center;
  opacity: 0.7;
}

.page-buttons__previous-page > img {
  height: 1.5625rem;
  width: 2.1875rem;
}

.page-buttons__next-page {
  position: absolute;
  right: 0px;
  height: 100%;
  margin-right: 0.5rem;
  display: flex;
  align-items: center;
  opacity: 0.7;
}

.page-buttons__next-page > img {
  height: 1.5625rem;
  width: 2.1875rem;
}

.page-buttons__previous-page > img:hover, .page-buttons__next-page > img:hover {
  background-color: rgba(167, 237, 255, 0.295);
  border-radius: 0.5rem;
  cursor: pointer;
}
/*конец стилей для paginator*/

.user-searcher {
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
  top: 0;
  width: 13.5rem;
