  align-items: center;
  justify-content: center;
  width: 1.4rem;
  height: 1.4rem;
  margin: 0 0.2rem 0 0.2rem;
}

.user-searcher__button-wrapper:hover {
  cursor: pointer;
  background-color: rgba(167, 237, 255, 0.295);
  border-radius: 0.5rem;
}

.user-searcher__button {
  position: absolute;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  z-index: -1;
}

.user-searcher__img {
  width: 1.125rem;
  height: 1.125rem;
  opacity: 0.7;
}

.user-searcher__img:hover {
  opacity: 1;
}

.user-searcher__input {
  width: 11rem;
  height: 1rem;
  border: solid 0.0625rem rgba(143, 143, 143, 0.144);
  border-radius: 0.25rem;
  background-color: rgba(143, 143, 143, 0.144);
  font-family: 'Cuprum', sans-serif;
  font-size: 1rem;
  padding-left: 0.2rem;
}

.user-searcher__input:focus {
  outline:none;
  background-color: white;
}

.users-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0.5rem;
  background-color: white;
  border: solid 0.0625rem rgba(128, 128, 128, 0.200);
  border-radius: 0.25rem;
  padding: 1rem;
}

.users-list__user {
  border: solid 0.0625rem rgba(128, 128, 128, 0.2);
  width: 9.3125rem;
  height: 12.5rem;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  margin: 0.25rem;
}

.users-list__user-avatar {
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  width: 100%;
  height: 70%;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  border-bottom: solid 0.0625rem rgba(128, 128, 128, 0.2);
  -webkit-transition: border-radius 500ms, -webkit-transform 500ms;
